import React from 'react';
import PropTypes from 'prop-types';
import '../header.less';
import '../reset.less';
import '../style.less';
import { books } from '../books.js';
import LazyLoad from 'react-lazy-load';
// import './jquery.js';
// import './modernizr.js';
import './scissor.js';
import './turn.js';

import './BookRead.less';
class BookRead extends React.Component {
  constructor(props, context) {
    super(props, context);
    this.state = {
    };
  }
  componentDidMount() {
    let book = books.find(b => b.uniqueName == this.props.params.uniqueid) ;
    this.setState({ b: book});
    document.title="Read "+book.name; 
    setTimeout(()=>this.loadApp(),0);
  }
  loadApp() {

    var flipbook = $('.flipbook');
  
     // Check if the CSS was already loaded
    
    if (flipbook.width()==0 || flipbook.height()==0) {
      setTimeout(loadApp, 10);
      return;
    }
  
    $('.flipbook .double').scissor();
  
    // Create the flipbook
  
    $('.flipbook').turn({
      width:window.innerWidth >800?800:window.innerWidth,
        
        // Height
  
        height:window.innerHeight,
        // Elevation
  
        elevation: 50,
        
        // Enable gradients
  
        gradients: true,
        // page:2,
        
        // Auto center this flipbook
  
        autoCenter: true
  
    });
  }
  
  render() {
    let b = this.state.b;
    return (
      b ? <div className="book-read"> 

<div class="flipbook-viewport">
	<div class="container">
		<div class="flipbook">
    <img className="page" src={b.url.replace('$pageno$', ('00' + '1').slice(-3))} alt={"Book "+b.name +" page "+(1)}/>
    {(new Array(b.pages-1)).fill(0).map((r, i) => <img className="double" src={b.url.replace('$pageno$', ('00' + (i + 2)).slice(-3))} alt={"Book "+b.name +" page "+(i+2)}/>)}
      </div>
      </div>

</div>        
        {/* <LazyLoad 
      debounce={false}
      offsetVertical={500}
      >
        <div className="pages">
          {(new Array(b.pages)).fill(0).map((r, i) => <img src={b.url.replace('$pageno$', ('00' + (i + 1)).slice(-3))} alt={"Book "+b.name +" page "+(i+1)}/>)}
        </div>
</LazyLoad>
         */}

      </div> : null);
  }
}

export default BookRead;
BookRead.contextTypes = {
  router: PropTypes.func.isRequired
};
